import React from 'react'
import { connect } from 'react-redux'
import { selectDatabase } from 'reducers/root-reducer'


import { plot } from 'sequences'


const Presentational = props => {
  return (
    <div>
      <div>
        Nombre de fichiers chargés : {props.nb_files}
      </div>
      <div className="mdl-layout-spacer"></div>

      <div className="mdl-cell mdl-cell--8-col" id="main" ref={(input) => {
          console.time('plot')
          plot(props.csv)
          console.timeEnd('plot')
        }}>
          <div id="sequence"></div>
          <div id="chart"></div>
      </div>

      <div className="mdl-layout-spacer"></div>

      <div className="mdl-cell mdl-cell--1-col" id="sidebar">

        <label className="mdl-switch mdl-js-switch mdl-js-ripple-effect" for="togglelegend">
          <input type="checkbox" id="togglelegend" className="mdl-switch__input" />
          <span className="mdl-switch__label">Légende</span>
        </label>

        <div id="legend" style={{visibility: 'hidden'}}></div>

      </div>

      <div className="mdl-layout-spacer"></div>
      
    </div>
  )
}



const mapStateToProps = state => {
  let database = selectDatabase(state)
  console.time('csv')
  let csv = database.toCsv()
  console.timeEnd('csv')

  return {
    csv,
    nb_files: database.size()
  }
}
 
const mapDispatchToProps = dispatch => {
  return {}
}


const Container = connect(
  mapStateToProps,
  mapDispatchToProps
)(Presentational)

export default Container
