// @flow

import React from 'react'
import ReactDOM from 'react-dom'
import { Provider } from 'react-redux'
import { createStore } from 'redux'

import rootReducer from 'reducers/root-reducer'

import MainSpace from 'components/main-space'


window.onload = function () {
  let root_div = document.createElement('div')
  root_div.setAttribute('id','root')

  if (document.body !== null) {
    document.body.append(root_div)
  }

  let store = createStore(rootReducer)

  window.swag = store

  ReactDOM.render(
    <Provider store={store}>
      <div className="demo-layout-transparent mdl-layout mdl-js-layout">

        <header className="mdl-layout__header mdl-layout__header--transparent">
          <div className="mdl-layout__header-row">

            <span className="mdl-layout-title">
              <a className="logo-espace mdl-navigation__link" href="index.html"><strong>archifiltre</strong></a>
            </span>

            <div className="mdl-layout-spacer"></div>

            <nav className="mdl-navigation">
              <p className="mdl-navigation__link" href=""><strong>Outils à tester |</strong></p>
              <a className="mdl-navigation__link" href="stalactite.html">stalactite</a>
              <a className="mdl-navigation__link" href="stalactite2.html">stalactite v2 (beta)</a>
            </nav>
          </div>
        </header>
        <main>
          <div className="mdl-grid">
            <div className="mdl-cell mdl-cell--12-col">
              <p>Avec Stalactite, découvrez vos répertoires sous un jour nouveau.<br /><em>(Compatible uniquement avec Chrome et Firefox)</em></p>
            </div>
            <div className="mdl-cell mdl-cell--12-col">
              <MainSpace />
            </div>
          </div>
        </main>

      </div>
    </Provider>,
    root_div
  )
}

